.title-area {
  display: flex;
  flex-direction: row;
}

.logo-img {
  padding-top: 12px;
  padding-right: 5px;
  height: 20px;
}

.horizontal-bar {
  background: black;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  height: 1px;
}

.arrow-right {
  margin-top: 2px;
  margin-left: 5px;
  margin-right: 5px;
  width: 12px;
  height: 12px;
}

.bottom-area {
  display: flex;
  justify-content: space-between;
}

.settings-button {
  background: transparent;
  border: none !important;
  color: white;
}

.settings-button-content {
  display: flex;
  flex-direction: row-reverse;
}

.disabled-warning {
  text-align: center;
  text-transform: capitalize;
}

.disabled-warning-subtext {
  padding-top: 10px;
}
