.api-key {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.api-key-input {
  margin-left: 10px;
}

.type-dropdown {
  text-transform: capitalize;
}
.type-dropdown:disabled {
  background: rgb(239, 239, 239);
  color: rgb(84, 84, 84);
}

.main-page-button {
  display: flex;
  background: transparent;
  border: none !important;
  color: white;
}

.settings-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.lock-area {
  display: flex;
  flex-direction: row-reverse;
}

.FAQ {
  display: flex;
  flex-direction: row-reverse;
}
